.header {
    display: flex;
    align-items: center;
    justify-content: space-between;

    padding-left: .5em;
    padding-right: .5em;

    &__logo {
        // width: 85px;
        // height: 39px;
        width: 5.5em;
        height: 2.5em;
    }

    &__logo-wrap {
        a {
            display: block;
        }
    }

    &__nav {}

    &__nav-list {
        display: flex;
        font-size: 1em;
    }

    &__nav-item {
        a {
            display: block;
            padding: 3px 15px;
            border-radius: 50px;
            text-decoration: none;
            color: var(--gray);
        }

        &--active a {
            background-color: var(--main-yello);
            color: var(--black);
        }
    }
}
