.menu {
    &__section {
        margin-top: 75px;
    }

    &__section h2,
    &__sub-header {
        font-family: $font-scnd;
        font-weight: 300;
        text-transform: uppercase;
    }

    &__section h2 {
        font-size: 1.25em;
        color: var(--main-yello);
        letter-spacing: 5px;
    }

    &__section h2,
    &__sub-header {
        text-align: center;
    }

    &__section h2 {
        margin-right: 2.5em;
    }

    &__sub-header {
        margin-right: 5em;
        // width: 82%;
    }

    &__header {}

    &__sub-header {
        margin-bottom: 5px;
        font-size: .75em;
        color: var(--gray);
        letter-spacing: 3px;
    }

    &__list {
        margin-top: 30px;

        &>* {
            &:not(:first-child) {
                margin-top: 35px;
            }
        }
    }

    &__item {
        text-transform: capitalize;
        color: var(--main-yello);
    }

    &__item-wrap {
        display: flex;
        justify-content: flex-end;
    }

    &__item-title-wrap {
        display: flex;
        align-items: center;
        justify-content: center;
        flex: 1;
        flex-direction: column;

        &>* {
            &:not(:first-child) {
                margin-top: 10px;
            }
        }
    }

    &__item-title {
        position: relative;
        width: 100%;
        text-align: center;
        font-size: 1.1em;
        padding: 0 1.5em;

        span {
            position: absolute;
            top: 50%;
            margin-left: 5px;

            font-family: $font-scnd;
            color: var(--gray);
            font-size: .60em;

            transform: translateY(-50%);
        }
    }

    &__item-price {
        display: flex;
        flex-direction: column;
        justify-content: center;

        padding-left: 5px;
        width: 4em;

        font-size: .95em;
        font-family: $font-scnd;
        color: var(--white);
        border-left: 2px solid var(--main-yello);

        span {
            // display: block;
            text-align: center;
        }

        &--ob {
            font-family: $font-scnd;
            color: var(--gray);
            font-size: .75em;
            text-transform: uppercase;
        }
    }

    &__rub {
        display: flex;
        justify-content: center;

        span {
            position: relative;
            margin-left: 3px;

            &::after {
                content: 'ла';
            }

            &::before {
                content: '';
                position: absolute;
                top: 60%;
                left: 0;
                width: 6px;
                height: 1.5px;
                background-color: currentColor;
                transform: translateY(-50%);
            }
        }
    }

    &__sub-list {}

    &__sub-item {
        margin-top: 5px;

        &--25 {
            margin-top: 25px;
        }
    }
}
