@charset "UTF-8";
:root { --main-yello: #FCBD63; --black: #020202; --gray: #757575; --white: #ffffff; }

.icon-facebook { width: 0.53em; height: 1em; fill: initial; }

.icon-facebook { width: 0.53em; height: 1em; fill: initial; }

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@media (prefers-reduced-motion: reduce) { * { transition: none !important; } }

@font-face { font-family: product; src: url("../fonts/product-sans.woff2"); font-weight: 400; font-style: normal; font-display: swap; }

@font-face { font-family: valencia; src: url("../fonts/valencia.woff2"); font-weight: 400; font-style: normal; font-display: swap; }

body { padding: 30px 10px; padding-bottom: 100px; font-size: 14px; line-height: 1.4; font-family: "product", sans-serif; background: #020202 url(./../img/bg.png) repeat-y; background-size: contain !important; }

@media only screen and (min-width: 375px) { body { font-size: 17px; } }

@media only screen and (min-width: 460px) { body { font-size: 22px; } }

.bg-p { position: relative; }

.bg-p--1 { padding-bottom: 250px; }

.bg-i { z-index: -1; position: absolute; left: -10px; right: -10px; width: calc(100% + 20px); height: 100%; background-size: cover; }

.bg-i--1 { bottom: -20px; background-image: url(./../img/bg-1.png); background-repeat: no-repeat; }

.sticky-nav { position: sticky; top: 25px; left: 0; }

.sticky-nav__list { position: absolute; left: -5px; top: -5px; transform-origin: left top; transform: rotate(-90deg) translateX(-100%); display: flex; }

.sticky-nav__list a { display: block; padding: 2px 5px 20px 5px; text-decoration: none; color: var(--gray); font-family: "valencia", sans-serif; font-size: .55em; letter-spacing: 2px; text-transform: uppercase; }

.sticky-nav__list a:active, .sticky-nav__list a:hover { color: var(--main-yello); }

.header { display: flex; align-items: center; justify-content: space-between; padding-left: .5em; padding-right: .5em; }

.header__logo { width: 5.5em; height: 2.5em; }

.header__logo-wrap a { display: block; }

.header__nav-list { display: flex; font-size: 1em; }

.header__nav-item a { display: block; padding: 3px 15px; border-radius: 50px; text-decoration: none; color: var(--gray); }

.header__nav-item--active a { background-color: var(--main-yello); color: var(--black); }

.menu__section { margin-top: 75px; }

.menu__section h2, .menu__sub-header { font-family: "valencia", sans-serif; font-weight: 300; text-transform: uppercase; }

.menu__section h2 { font-size: 1.25em; color: var(--main-yello); letter-spacing: 5px; }

.menu__section h2, .menu__sub-header { text-align: center; }

.menu__section h2 { margin-right: 2.5em; }

.menu__sub-header { margin-right: 5em; }

.menu__sub-header { margin-bottom: 5px; font-size: .75em; color: var(--gray); letter-spacing: 3px; }

.menu__list { margin-top: 30px; }

.menu__list > *:not(:first-child) { margin-top: 35px; }

.menu__item { text-transform: capitalize; color: var(--main-yello); }

.menu__item-wrap { display: flex; justify-content: flex-end; }

.menu__item-title-wrap { display: flex; align-items: center; justify-content: center; flex: 1; flex-direction: column; }

.menu__item-title-wrap > *:not(:first-child) { margin-top: 10px; }

.menu__item-title { position: relative; width: 100%; text-align: center; font-size: 1.1em; padding: 0 1.5em; }

.menu__item-title span { position: absolute; top: 50%; margin-left: 5px; font-family: "valencia", sans-serif; color: var(--gray); font-size: .60em; transform: translateY(-50%); }

.menu__item-price { display: flex; flex-direction: column; justify-content: center; padding-left: 5px; width: 4em; font-size: .95em; font-family: "valencia", sans-serif; color: var(--white); border-left: 2px solid var(--main-yello); }

.menu__item-price span { text-align: center; }

.menu__item-price--ob { font-family: "valencia", sans-serif; color: var(--gray); font-size: .75em; text-transform: uppercase; }

.menu__rub { display: flex; justify-content: center; }

.menu__rub span { position: relative; margin-left: 3px; }

.menu__rub span::after { content: 'ла'; }

.menu__rub span::before { content: ''; position: absolute; top: 60%; left: 0; width: 6px; height: 1.5px; background-color: currentColor; transform: translateY(-50%); }

.menu__sub-item { margin-top: 5px; }

.menu__sub-item--25 { margin-top: 25px; }

.slide-i-l-1 { animation: slide-i-l 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both; animation-delay: 0.2s; }

.slide-i-l-2 { animation: slide-i-l-2 0.75s cubic-bezier(0.25, 0.46, 0.45, 0.94) both; animation-delay: 0.5s; }

.slide-i-l { animation: slide-i-l 0.75s cubic-bezier(0.25, 0.46, 0.45, 0.94) both; animation-delay: 1.5s; }

.menu__section { animation: slide-i-b 0.75s cubic-bezier(0.25, 0.46, 0.45, 0.94) both; }

.menu__section:nth-child(2) { animation-delay: 1s; }

.menu__section:nth-child(3) { animation-delay: 1.25s; }

@keyframes slide-i-l { 0% { transform: translateX(-100px);
    opacity: 0; }
  100% { transform: translateX(0);
    opacity: 1; } }

@keyframes slide-i-l-2 { 0% { transform: translateX(-30px);
    opacity: 0; }
  100% { transform: translateX(0);
    opacity: 1; } }

@keyframes slide-i-b { 0% { transform: translateY(50px);
    opacity: 0; }
  100% { transform: translateY(0);
    opacity: 1; } }
