body {
    padding: 30px 10px;
    padding-bottom: 100px;
    font-size: 14px;
    line-height: 1.4;

    font-family: $font-main;

    background: #020202 url(./../img/bg.png) repeat-y;
    background-size: contain !important;
}

@include rmin($bp-phone-s) {
    body {
        font-size: 17px;
    }
}

@include rmin($bp-phone-m) {
    body {
        font-size: 22px;
    }
}

.bg-p {
    position: relative;

    &--1 {
        padding-bottom: 250px;
    }
}

.bg-i {
    z-index: -1;
    position: absolute;
    left: -10px;
    right: -10px;

    width: calc(100% + 20px);
    height: 100%;
    background-size: cover;

    &--1 {
        bottom: -20px;
        background-image: url(./../img/bg-1.png);
        background-repeat: no-repeat;
    }
}

.sticky-nav {
    position: sticky;
    top: 25px;
    left: 0;

    &__list {
        position: absolute;
        left: -5px;
        top: -5px;
        transform-origin: left top;
        transform: rotate(-90deg) translateX(-100%);

        display: flex;

        li {}

        a {
            display: block;
            padding: 2px 5px 20px 5px;
            text-decoration: none;
            color: var(--gray);

            font-family: $font-scnd;
            font-size: .55em;
            letter-spacing: 2px;
            text-transform: uppercase;

            &:active,
            &:hover {
                color: var(--main-yello);
            }
        }
    }
}
